var  graceChecker = require("../../utils/graceChecker.js");
export default {
	data() {
		return {
			// picker: ['无','手表', '钥匙', '校园卡','银行卡','耳机','钱包'],
			picker:[{'id':1,name:'不限'},{'id':2,name:'披风'},{'id':3,name:'鞋子'}],
			time: '12:01',
			date: '2018-12-25',
			textareaAValue: '',
			imgList: [],
			pickerIndex:0,
			// loseTime:this.date+" "+this.time+":00",
			form: {
				lostname: "",
				lostPlace: '',
				type: 1,
				loseTime:'',
				details:'',
				contact: '',
				image:[],
				postType:'',
				
			}

		}
	},
	methods: {
		subInfo:function(){
			var rule = [
			                    {name:"lostname", checkType : "notnull", checkRule:"",  errorMsg:"请输入失物名称"},
			                    {name:"contact", checkType : "string", checkRule:"11",  errorMsg:"请输入正确格式的联系方式"},
								{name:"findname", checkType : "notnull", checkRule:"",  errorMsg:"请输入拾物名称"},
								{name:"QQ", checkType : "string", checkRule:"6,11",  errorMsg:"请输入QQ号"}
			                ];
							let form = {
								lostname:'小黄的女朋友',
								findname:'小黄的未来女朋友',
								contact:'13643662891',
								QQ:'695489479'
								
								
							}
							var formData =this.form;
							
							                var checkRes = graceChecker.check(formData, rule);
							                if(checkRes){
							                    uni.showToast({title:"发布成功!", icon:"none"});
							                }else{
							                    uni.showToast({ title: graceChecker.error, icon: " " });
							                }
							            },
		onClick(event) {
			wx.showToast({
				title: `点击标签 ${event.detail.name}`,
				icon: 'none'
			});
		},

		TimeChange(e) {
			this.time = e.detail.value
		},
		DateChange(e) {
			this.date = e.detail.value
		},
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType:["compressed"],
				success: (res) => {
					console.log(res)
					  const tempFilePaths = res.tempFilePaths
					uni.uploadFile({
						url:"http:localhost:8888/upload/img",
						filePath:tempFilePaths[0],
						name:"img",
						fileType:"image",
						// files:res.tempFiles,
						success:(uploadFileRes) => {
							let data=JSON.parse(uploadFileRes.data)
							console.log(data.status)
							if(data.status === 0){
								this.imgList.push("http://localhost:8888/img/"+data.data)
							}else{
								
							}						
						}						
					})
				}
			});
		},
		textareaAInput(e) {
			this.textareaAValue = e.detail.value
		},
		textareaBInput(e) {
			this.textareaBValue = e.detail.value
		},
		DelImg(e) {
			uni.showModal({

				content: '要删除这张照片吗？',
				cancelText: '取消',
				confirmText: '删除',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		PickerChange(e) {
			console.log(e)
			this.form.type=this.picker[e.detail.value].id
			this.pickerIndex = e.detail.value
		},

		subInfo: function() {
			this.form.postType =  this.active
			this.form.loseTime = this.date+" "+this.time+":00"
			let image = ''
			for(let i = 0;i<this.imgList.length;i++){
				if(i === this.imgList.length-1){
					image +=this.imgList[i]
					continue
				}
				image +=this.imgList[i] + '&&&'
			}
			this.form.image = image
			this.$api.addPost(this.form).then(res => {
				console.log(this.form)
					// uni.showModal({
					// 	content: '发表成功',
					// 	showCancel: false,
					// });
					
			})
		}

	}

}
